h1 {
  text-align: center;
  color: #fffffe;
}

h2 {
  color: #1D355D;
  font-weight: 600;
  font-size: 18px;
  line-height: 27px;
}

main {
  margin: 16px;
}

.container {
  background: #fffffe;
  background-repeat: no-repeat;
  background-size:  100% 100%;
  border-radius: 24px;
  margin-top: 60px;
  padding: 16px;
}

fieldset,
.bg {
  border-color: transparent;
}

form {
  display: flex;
  flex-direction: column;

  button {
    font-style: normal;
    font-weight: 500;
    font-size: 18px;
    line-height: 27px;
    color: rgba(96, 102, 208, 0.8);
    border: 2px solid rgba(96, 102, 208, 0.7);
    box-sizing: border-box;
    border-radius: 12px;
    margin-top: 32px;
  }
}

button {
  padding-top: 14px;
  padding-bottom: 15px;
}

.greenBg {
  background-color:'#81C784';
}

.redBg {
  background-color: '#FF8A65'
}

button:hover,
.nextBtn {
  background: #F9A826;
}

.nextBtn {
  box-shadow: 0px 2px 4px rgba(252, 168, 47, 0.4);
  border-radius: 12px;
  padding-left: 37px;
  padding-right: 37px;
}

.showNextBtn {
  display: block;
}

.hideNextBtn {
  display: none;
}

@media (min-width: 1000px) {
  .container {
    max-width: 600px;
    margin-left: auto;
    margin-right: auto;
  }
}
